class AboutPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      popupActive: false
    }
  }
  componentDidMount = async () => {
    // open popup after viewer sees the aboutus content
    if (this.props.open_contact) {
      setTimeout(() => {
        this.setState({ popupActive: true })
      }, 500)
    }
  }
  componentDidUpdate = async (prevProps) => {
    // open popup immediately
    if (this.props.open_contact && this.props.open_contact !== prevProps.open_contact) {
      this.setState({ popupActive: true })
    }
  }
  render() {
    return (
      <>
        <Header location={this.props.location} standalone={true} />

        {/*
         * Page content
         */}
        <AboutUs className="content">
          <div className="before">
            <div className="titleWithButton">
              <h2>About us:</h2>
              <ContactUsButton
                className="ContactUsButton clickable"
                onClick={() => {
                  this.setState({ popupActive: true })
                }}
              >
                Contact us&thinsp;
                <FA icon={faPhoneAlt} className="" />
              </ContactUsButton>
            </div>
            <p>
              A husband and wife team, based in Kansas City, MO (USA). üë®üèº‚Äçüíªüë©üèΩ‚Äçüíº Combining our talents, we were able to make
              the world's best English language thesaurus. Paul built some advanced custom admin tools. Samira used them
              to comb through and edit thousands of words.
            </p>
            <ImgCarousel />
          </div>
          <p className="bio">
            <span className="color-attention">
              <b>Paul Shorey</b> has worked 11 years with web development and software engineering teams in NYC, Utah,
              California, and remotely.
            </span>{" "}
            Finally getting pretty good at coding. He specializes in front-end and back-end Javascript,
            microservices/API development, and UI/UX design. Self-taught, Paul actually graduated from art school. There
            is very much similarity between fine-art and tech/software... {/*<span className="readMore">*/}
            {/*  read more <FA icon={faAngleDown} className="" />*/}
            {/*</span>*/}
          </p>
          <p className="bio">
            <span className="color-attention">
              <b>Samira Ali</b> has a degree in Sociology.
            </span>{" "}
            She speaks Swahili and Mandarin, can read/write Arabic, and is learning Spanish. She has worked for several
            non-profits around Kansas City, including United Inner City Services, and has even led her own initiatives.
            Samira has worked as a social media coordinator, graphic designer, researcher, and an electronic health
            records technician. Samira just wants to help people... {/*<span className="readMore">*/}
            {/*  read more <FA icon={faAngleDown} className="" />*/}
            {/*</span>*/}
          </p>
          <br />
          <div className="before">
            <div className="titleWithButton">
              <h2>This is a work in progress...</h2>
            </div>
            <p>
              Currently finishing up the domain suggestions app and API -{" "}
              <a href="https://nlpthesaurus.com" target="_blank">
                NLP.Domains
              </a>
              <br />
              Also launching the world's best English Language Thesaurus for business and AI applications -{" "}
              <a href="https://nlpthesaurus.com" target="_blank">
                NLP
                <_ />
                Thesaurus
                <_ />
                .com
              </a>
              <br />
              Coming soon - more nifty domain name tools, then text and sentiment analysis APIs, and then hopefully some
              content based projects -{" "}
              <a href="https://nlp.studio" target="_blank">
                NLP.Studio
              </a>
            </p>
          </div>
          <div className="after">
            <p className="bio">
              This is not a get rich quick scheme for us, but a journey. Thank you for your visit today. Thank you for
              your support.
            </p>
            <p className="bio">
              Please do contact us, for any reason. What is your first impression of the site, of the suggestions, the
              thesaurus? Please do try it, a few times. Please let us know if one of the search terms did not give good
              results. We're still working on it.
            </p>
            <p className="bio">
              Lets work together! Let's build something together. We can put our future plans (after
              nlp.domains/nlpthesaurus) on hold to build something for you, with your team! Hire us. Partner with us.
              Invest in us. Be our friend. :)
            </p>
            {/*<p className="bio">*/}
            {/*  <b>Please join our mailing list,</b> for very in-frequent emails - to be the first to know when we launch*/}
            {/*  a new product or major feature.*/}
            {/*</p>*/}
          </div>
          <div className="after">
            <ContactUsButton
              className="ContactUsButton clickable"
              onClick={() => {
                this.setState({ popupActive: true })
              }}
            >
              Contact us&thinsp;
              <FA icon={faPhoneAlt} className="" />
            </ContactUsButton>
          </div>
          <p>&nbsp;</p>
          <p>&nbsp;</p>
          <p>&nbsp;</p>
        </AboutUs>
        {/*
         * Contact popup
         */}
        <ContactPopup
          show={!!this.state.popupActive}
          onClose={() => {
            this.setState({ popupActive: false })
          }}
        />
      </>
    )
  }
}
export default AboutPage

